.dashBoard_ref_area1 {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.referral_banner_bg_div {
  width: 100%;
}

.referral_banner_bg_img {
  width: 100%;
}

.welcome_bonus_div {
  align-items: flex-end !important;
}

.dashBoard_ref_area1_cont1 {
  width: 100%;
  display: flex;
  align-items: center;
  background: #fff;
  padding: 1em;
  border-radius: 8px;
  gap: 25px;
  border: solid 1px #dddddd;
  box-shadow: 0px 6px 10px #d5d5d5;
  overflow: scroll;
  align-items: center;
}

.dashBoard_ref_area1_cont1_div1_cont1_withdraw_btn {
  padding: 10px;
  width: 119px;
  font-size: 14px;
  border-radius: 7px;
  border: none;
  background: #7a5fc0;
  color: #fff;
}

.dashBoard_ref_area1_cont1_div1_cont1_withdraw_btn:disabled {
  background: #c5c4d6;
  color: #fbfbff;
  cursor: not-allowed;
}

.dark .dashBoard_ref_area1_cont1_div1_cont1_withdraw_btn:disabled {
  background: #3b3959;
  color: #676588;
}

.dashBoard_ref_area1_cont2 {
  display: flex;
  width: 50%;
  padding: 1em;
  background: #fff;
  border-radius: 10px;
  grid-gap: 25px;
  gap: 20px;
  align-items: center;
  justify-content: flex-start;
  border: solid 1px #dddddd;
  box-shadow: 0px 6px 10px #d5d5d5;
}

.dashBoard_ref_area1_cont1_icon_div {
