  font-size: 38px !important;
  border-radius: 50%;
  box-shadow: 0px 3px 6px #7c7a9c;
  color: #7a5fc0;
}

.dashBoard_ref_area1_cont1_div1 {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
}

.dashBoard_ref_area1_cont1_div1_cont1 {
