.ref_btn .tooltiptext {
  display: none;
  width: 140px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 200%;
  left: 26%;
  margin-left: -75px;
  opacity: 1;
  transition: opacity 0.3s;
}

.payViaFortDiv_area_3_input_cont_btn .tooltiptext2 {
  display: none;
  width: 140px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 106%;
  left: -100%;
  margin-left: -100px;
  opacity: 1;
  transition: opacity 0.3s;
}

.tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltiptext2::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.ref_btn {
  padding: 1em 0em;
  background: #7a5fc0;
  color: #fff;
  font-weight: 600;
  border: none;
  border-radius: 10px;
  box-shadow: 0px 5px 20px #73a988;
}

.ref_btn {
  position: relative;
}

.tooltip {
  position: relative;
  display: inline-block;
}

